				setData(items) {
					let newItems = [];
					items.forEach((e) => {
						newItems.push({
							text: e.text,
							value: e.value
						});
					});
					return newItems;
				},
				setData2(items) {
					let newItems = [];
					items.forEach((e) => {
						newItems.push({
							text: e.text,
							text2: e.text2,
							value: e.value
						});
					});
					return newItems;
				},
