					let measuringOff = uni.getStorageSync('MeasuringListOffLine');
					if (measuringOff) {
						console.log(measuringOff);
						measuringOff.push(tempObject)
					}else{
						measuringOff=[];
						measuringOff.push(tempObject)
					}
					uni.setStorageSync("MeasuringListOffLine",measuringOff);
					uni.showToast({
						title: '暂存成功',
						mask: false,
						duration: 1500
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 2000)
				}
		}
	}



